<template>
  <div id="app">
    <transition :name="transitionName">
      <keep-alive v-if="$route.meta.needStore">
        <router-view></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.needStore"></router-view>
    </transition>
  </div>
</template>

<script>
import { initWechatJs } from '@/common/wechat'
import { _parseJSON } from '@/common/utils'
export default {
  name: 'App',
  data () {
    return {
      transitionName: ''
    }
  },
  mounted () {
    // this.$nextTick(function () {
    this.initWechatJs()
    // })
  },
  watch: {// 使用watch 监听$router的变化
    $route (to, from) {
      // 如果to索引大于from索引,判断为前进状态,反之则为后退状态
      if (to.meta.index > from.meta.index) {
        this.transitionName = 'slide-left'
      } else {
        this.transitionName = 'slide-right'
      }
    }
  },
  methods: {
    initWechatJs () {
      this.axios.post('http://coach.realmshow.com/api/webchat-api/pub/signature').then((res) => {
        if (res.data.code === 200) {
          var wechatSign = _parseJSON(res.data.data)
          console.log(wechatSign)
          initWechatJs(wechatSign.appId, wechatSign.nonce, wechatSign.timestamp, wechatSign.signature)
        }
      }).catch((response) => {
        console.log(response)
      })
    }
  }
}
</script>

<style lang='scss'>
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
    .slide-right-enter-active,
    .slide-right-leave-active,
    .slide-left-enter-active,
    .slide-left-leave-active {
      will-change: transform;
      transition: all 500ms;
      position: absolute;
    }
    .slide-right-enter {
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
    }
    .slide-right-leave-active {
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
    .slide-left-enter {
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
    .slide-left-leave-active {
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
    }
  }
}
button {
  border: 0;
  width: 160px;
  height: 50px;
  line-height: 50px;
  background-color: #000000;
  color: #ffffff;
  border-radius: 50px;
}
.mint-indicator {
  .mint-indicator-wrapper {
    z-index: 9999;
    .mint-indicator-text {
      font-size: 10px !important;
    }
  }
}
</style>



